@charset "utf-8";

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
}

a{
    color: #2c2c2c;
    font-weight: 700;
}


html,body,#root,.container{
    height: 100%;
    display: flex;
    justify-content: center;
    background-color: #ddd;
}

.container {
    height: 100%;
    display: flex;
    justify-content: center;
  }


.nav{
    position: fixed;
    top: 20px;
    left: 20px;
    width: 100px;
    height: 100px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    border-radius: 5px;
    box-shadow : 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}

.center{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.movies {
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 80%;
    padding-top: 70px;
  }

  @media screen and (max-width: 1090px) {
    .movies {
      grid-template-columns: 1fr;
      width: 100%;
    }
  }

.movies .movie{
    background-color: #fff;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    /* justify-content: space-between; */
    font-weight: 300;
    padding: 20px;
    color:#adaeb9;
    border-radius: 5px;
    box-shadow : 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}

.movie img{
    position: relative;
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow : 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}

.movie .movie__title, .movie .movie__year{
    margin: 0;
    font-weight: 300;
}

.movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
    color:#2c2c2c;
}

 .movie .movie__genres{
     list-style: none;
     padding: 0;
     margin: 0;
     display: flex;
     margin: 5px 0;
}

.movie__genres li{
    margin-left: 10px;
    font-size: 14px;
}